.remp-form-container .checkbox-container {
  display: block;
  position: relative;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.remp-form-container .registration-checkbox-container {
  margin-bottom: 25px;
}

.remp-form-container .checkbox-container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.remp-form-container .checkmark {
  height: 15px;
  width: 15px;
  background-color: transparent;
  border: 1px solid #3f3f447a;
  display: inline-block;
  margin-right: 5px;
  margin-bottom: -3px;
  border-radius: 3px;
}

.remp-form-container .checkbox-container:hover input ~ .checkmark {
  background-color: #f3f3f3;
}

.remp-form-container .checkbox-container input:checked ~ .checkmark {
  background-color: #fcbf1e;
  border-color: #3f3f44;
}

.remp-form-container .checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

.remp-form-container .checkbox-container input:checked ~ .checkmark:after {
  display: block;
}

.remp-form-container .checkbox-container .checkmark:after {
  left: 6px;
  top: 3px;
  width: 4px;
  height: 7px;
  border: solid #3f3f44;
  border-width: 0 1px 1px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}

.remp-form-container .has-error .checkbox-container input ~ .checkmark {
  background-color: #ffabbc57;
  border-color: #ab1414;
}

.remp-form-container .has-error input {
  border-color: #ab1414;
  background-color: #ffabbc57;
  -webkit-box-shadow: none;
  box-shadow: none;
}

.btn-primary {
  font-family: "Roboto Slab", serif;
  background-color: #3f3f44;
  color: #f7f7f7;
  border: none;
  font-weight: bold;
  text-transform: uppercase;
  outline: none;
  font-size: 15px;
  letter-spacing: 1px;
  border-radius: 5px;
  transition: all, 0.3s;
  width: 100%;
  height: 39px;
  white-space: unset;
}
.btn-primary:hover,
.btn-primary:focus {
  background-color: #000056;
  color: #fd014b;
}
.btn-primary:active{
  background-color: #000056;
  color: #fd014b;
}

