.thank-you-list {
  text-align: left;
  margin-top: 45px;
}

.thank-you-list li {
  list-style: none;
  position: relative;
  padding-left: 30px;
  font-weight: 700;
  margin-bottom: 35px;
}

.thank-you-list li::before {
  content: "";
  width: 20px;
  height: 20px;
  display: inline-block;
  border: 1px solid #3f3f44a8;
  position: absolute;
  top: 4px;
  left: 0;
  border-radius: 10px;
}

.thank-you-list li::after {
  content: "";
  width: 14px;
  height: 14px;
  display: inline-block;
  background-color: #3f3f44a1;
  position: absolute;
  top: 7px;
  left: 3px;
  border-radius: 7px;
}

.thank-you-list li span {
  display: block;
  font-weight: 400;
}

.thank-you-list li span::before {
  content: "";
  width: 1px;
  height: 100%;
  background-color: #3f3f44a8;
  display: block;
  position: absolute;
  left: 10px;
  top: 32px;
}

.thank-you-list li:last-child span::before {
  display: none;
}

.thank-you-list.horizontal {
  display: flex;
  justify-content: space-between;
  margin-block-start: 0;
  margin-block-end: 0;
  margin-inline-start: 0;
  margin-inline-end: 0;
  padding-inline-start: 0;
  margin-bottom: 30px;
  padding-top: 30px;
}

.thank-you-list.horizontal li {
  flex-basis: 1;
  width: 33%;
  padding-left: 0;
  margin-bottom: 0;
  text-align: center;
}

.thank-you-list.horizontal li::before {
  top: -30px;
  left: 0;
  right: 0;
  margin: 0 auto;
}

.thank-you-list.horizontal li::after {
  top: -27px;
  left: 0;
  right: 0;
  margin: 0 auto;
}

.thank-you-list.horizontal li span::before {
  width: calc(100% - 35px);
  height: 1px;
  left: calc(50% + 20px);
  top: -21px;
}

.thank-you-list.horizontal li:last-child span::before {
  display: none;
}

